<script>
import { Carousel, Slide } from "vue-carousel";
import axios from "axios";
import firebase from "firebase";
let db = firebase.firestore();
// let rdb = firebase.database()
import autophp from "@/plugins/autophp";
let sdb = new autophp();
export default {
  data() {
    return {
      series: [60, 60, 50, 40, 30],
      chartOptions: {
        chart: {
          width: "100%",
          type: "pie",
        },
        labels: ["WEB", "MOBILE", "DESKTOP", "IOT", "AUTOMATION"],
        theme: {
          monochrome: {
            enabled: true,
          },
        },
        plotOptions: {
          pie: {
            dataLabels: {
              offset: -5,
            },
          },
        },
        title: {
          text: "Number of leads",
        },
        dataLabels: {
          formatter(val, opts) {
            const name = opts.w.globals.labels[opts.seriesIndex];
            return [name, val.toFixed(1) + "%"];
          },
        },
        legend: {
          show: false,
        },
      },
    };
  },
  components: {
    Carousel,
    Slide,
  },
  layout: "author",
  methods: {},
  mounted() {
    setTimeout(() => {
      this.$el.querySelectorAll(".apexcharts-legend-text").forEach((e) => {
        e.classList.add("");
      });
      this.$el.querySelectorAll(".VueCarousel-dot").forEach((e) => {
        e.setAttribute("style", "color:transparent");
      });
      //  .classList.add('text-dark')
      //
    }, 2000);
  },
  metaInfo() {
    return {
      
      title: "Portofolio TA_Maliki / Taufik Akbar Maliki PORTOFOLIO WEBSITE OFFICIAL",
      meta: [
        {
          hid: "description",
          name: "description",
          content: "Portofolio TA_Maliki / Taufik Akbar Maliki PORTOFOLIO WEBSITE OFFICIAL ",
        },
        {
          property: "og:title",
          content: "Portofolio TA_Maliki / Taufik Akbar Maliki PORTOFOLIO WEBSITE OFFICIAL",
        },
        { property: "og:site_name", content: "TA_Maliki" },
